  height: 56px;
  box-sizing: border-box;
  background-image: linear-gradient(#000 64px, rgba(0, 0, 0, 0) 0), linear-gradient(#000 66px, rgba(0, 0, 0, 0) 0), radial-gradient(4px, #000 100%, rgba(0, 0, 0, 0) 0);
